import config from './config'
import axios from 'axios'
require('./DataTable/jquery-3.2.1.min.js')
// axios.defaults.withCredentials = true

let prefix = config.url + ':' + config.port + '/api'

export default {
  isLogin () {
    return $.get(prefix + '/isLogin');
  },
  signIn (number, password) {
    return $.post(prefix + '/signIn', {
      number: number,
      password: password
    })
  },
  signUp (number, password) {
    return axios.post(prefix + '/signUp', {
      number: number,
      password: password
    })
  },
  books (flag)  {
    return axios.get(prefix + '/books', {
      params: {
        flag: flag        
      }
    })
  },
  book (number) {
    return axios.get(prefix + '/book', {
      params: {
        number: number
      }
    })
  },
  getUser (number) {
    return axios.get(prefix + '/user', {
      params: {
        number: number
      }
    })
  },
  reserve (userNumber, bookNumber) {
    return axios.get(prefix + '/book-reserve', {
      params: {
        userNumber: userNumber,
        bookNumber: bookNumber
      }
    });
  },
  download: prefix + '/file'
}



