<template lang="jade">
  div#head
    img(src='../../assets/logo.jpg', @click="admin")
    span#title {{title}}
    span#login(@click="login" v-if="!user") 登录
    div#info
      span#name(v-if="user") {{user.name}}
      el-button#logout(v-if="user") 退出

</template>
<script>
  import api from '../../common/api'
  export default {
    name: 'header',
    props: ['user'],
    data () {
      return {
        title: '中大图书馆系统',
      }
    },
    methods: {
      admin () {
        this.$router.push({
          name: 'admin'
        })
      },
      login () {
        this.$router.push({
          name: 'Login'
        })
      }
    }
  }
</script>
<style lang="sass" scoped>
#head
  width: 100%
  position: relative
  height: 4rem
  background-color: #0bdbdc
  #title
    position: absolute
    line-height: 4rem
    left: 50%
    font-size: 2rem
    color: #0647ff
    transform: translateX(-50%)
  #login
    position: absolute
    line-height: 4rem
    right: 2rem
    color: #0647ff
  #info
    display: inline-block
    position: absolute
    right: 2rem
    line-height: 4rem
    #name
      margin-right: 1rem
      font-size: 1.5rem
  img
    width: 4rem
    height: 4rem
</style>



